@import "../../../styles/helpers/_helpers";

.block-production {
	display: grid;
	grid-template-columns: repeat(var(--grid-column), 1fr);
	gap: var(--grid-gap);
	.block__header {
		display: contents;
	}
	.block__content {
		display: contents;
	}
	h2 {
		grid-column: span 6;
	}
	p {
		margin: 0;
		grid-column: span 8;
	}
	img {
		grid-row: 1/4;
		grid-column: 9/span 4;
		align-self: center;
	}
}
